import * as React from "react";
import { usePaginationContext } from "./utils/context";
export default function PaginationView() {
  const {
    page,
    totalPages,
    setPage,
    handler,
    nextPageHandler,
    prevPageHandler,
    pageButtons,
  } = usePaginationContext();
  return (
    <div
      style={{
        marginTop: "18px",
        display: "flex",
        justifyContent: "space-between",
      }}
    >
      <p
        style={{
          fontSize: "14px",
        }}
      >
        Page {page} of {totalPages}
      </p>
      <div
        style={{
          display: "flex",
        }}
      >
        <button
          onClick={prevPageHandler}
        //   disabled={page <= 1}
          style={{
            height: "38px",
            width: "90px",
            backgroundColor: "#fff",
            border: "1px solid #D0D5DD",
            boxShadow: "0px 1px 2px rgba(16, 24, 40, 0.05)",
            borderRadius: "6px",
            fontSize: "14px",
            fontWeight: "normal",
          }}
        >
          Previous
        </button>
        <div
          style={{
            margin: "0 8px",
            display: "flex",
          }}
        >
          {pageButtons.map((pageNum, id) => {
            return (
              <button
                key={id}
                onClick={() => {
                  handler(pageNum);
                }}
                style={{
                  width: "40px",
                  height: "40px",
                  fontWeight: "normal",
                  border:'none',
                  color: page == pageNum ? "#3762DD" : "inherit",
                  backgroundColor: page == pageNum ? "#EDF1FF" : "inherit",
                  fontSize: page == pageNum ? "14px" : "inherit",
                }}
              >
                {pageNum}
              </button>
            );
          })}
        </div>
        <button
        //   disabled={page >= totalPages}
          onClick={nextPageHandler}
          style={{
            height: "38px",
            width: "90px",
            backgroundColor: "#fff",
            border: "1px solid #D0D5DD",
            boxShadow: "0px 1px 2px rgba(16, 24, 40, 0.05)",
            borderRadius: "6px",
            fontSize: "14px",
            fontWeight: "normal",
          }}
        >
          Next
        </button>
      </div>
    </div>
  );
}
